.home {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  gap: 2rem;
  padding: 3rem 2rem;
  min-height: 100vh;
  width: 101%;
  background-color: #1c1d30;
  color: white;
}

.text-big {
  font-size: 6rem;
}

.text-small {
  font-size: 2.4rem;
  padding: 1rem 0;
}

.content {
  flex: 1 1 42rem;
}
.btn {
  padding: 0.7rem 3rem;
  margin-top: 23px;
  font-size: 2rem;
  color: #1c1d30;
  background: white;
  border-radius: 5rem;
  cursor: pointer;
  box-shadow: 0 0.1rem 0.3rem rgba(0, 0, 0, 0.3);
  position: relative;
  overflow: hidden;
  z-index: 0;
}

.glow-on-hover {
  width: 220px;
  height: 50px;
  font-size: 20px;
  border: none;
  outline: none;
  color: #fff;
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
}

.glow-on-hover:before {
  content: "";
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

.glow-on-hover:active {
  color: #000;
}

.glow-on-hover:active:after {
  background: transparent;
}

.glow-on-hover:hover:before {
  opacity: 1;
}

.glow-on-hover:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #111;
  left: 0;
  top: 0;
  border-radius: 10px;
}

.middle-home {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  gap: 2rem;
  padding: 3rem 7rem;
  min-height: 100vh;
  color: white;
}

.middle-content {
  flex: 1 1 42rem;
  color: #1c1d30;
}
